<template>
  <div v-if="user && user.role == 'student'">
    <h2 style="color: #347c99d6">
      Страница просмотра журнала учёта посещаемости и успеваемости для студента
    </h2>
    <form class="info-for-completion">
      <h2>ФИО студента:</h2>
      <select @change="selectedStud()" v-model="student">
        <option disabled value="">Студент</option>
        <option :value="user.id" required>{{ user.username }}</option>
      </select>
      <h2>Группа:</h2>
      <select @change="selectedGroup()" v-model="gr">
        <option disabled value="">Группа</option>
        <option :value="user.group" required>{{ user.group }}</option>
      </select>
      <h2>Выберите период, за который хотите получить данные:</h2>
      <div class="dateformat">
        <h2 class="dateitem">C:</h2>
        <input
          type="date"
          v-model="date1"
          required
          pattern="\d{4}-\d{2}-\d{2}"
          class="dateitem"
        />
        <h2 class="dateitem">По:</h2>
        <input
          type="date"
          v-model="date2"
          required
          pattern="\d{4}-\d{2}-\d{2}"
          class="dateitem"
        />
      </div>
      <h2>Дисциплина:</h2>
      <select @change="selectedDisc" v-model="discz">
        <option disabled value="">Выберите дисциплину</option>
        <option v-for="req in discs" :key="req.id" :value="req.id">
          {{ req.name }}
        </option>
      </select>
    </form>
  <br/>
    <button
      :disabled="button()"
      type="button"
      class="btn"
      @click="getJournalST(this.worklodid3, this.student)"
      style="margin-left: 10px"
    >
      Найти журнал
    </button>
    <button type="button" class="btn" @click="Abnull()">Сбросить данные</button>
    <div v-if="this.loading == true && this.r == false">
      <h1 style="color: #347c99">Загрузка ...</h1>
    </div>
    <transition>
      <div v-if="r === true && journalGroup.length != 0">
        <p>Обозначения сокращений:</p>
        <p>
          Отметки посещаемости - "П", баллы успеваемости - "Б", задание или тема
          занятия (не более 100 знаков) - "З/Т"
        </p>
        <p>
          Отметки посещаемости: присутствие - "П", отсутствие - "О", отсутствие
          по уважительной причине - "ОПУ", отсутствие по болезни - "ОПБ"
        </p>
        <table class="iksweb">
          <tbody>
            <tr>
              <th rowspan="3">№</th>
              <th>Дата</th>
              <th
                v-for="(val, id) in journalGroup.slice(this.gran1, this.gran2)"
                :key="id"
                :value="id"
              >
                {{ val.dateform }}
              </th>
              <th rowspan="3">
                Итоговый балл за период 
                <br>
                с 
                <br>
                {{ this.date1 }} 
                <br>
                по 
                <br>
                {{ this.date2 }}
              </th>
            </tr>

            <tr>
              <th>Вид занятия</th>
              <th
                v-for="(val, id) in journalGroup.slice(this.gran1, this.gran2)"
                :key="id"
                :value="id"
              >
                {{ val.type }}
              </th>
            </tr>
            <tr>
              <th>ФИО студента</th>
              <th
                v-for="(req, id) in journalGroup.slice(this.gran1, this.gran2)"
                :key="id"
                :value="id"
              >
                <div class="otmetki">
                  <div class="ych">П</div>
                  <div class="ych">Б</div>
                  <div class="ych">З/Т</div>
                </div>
              </th>
            </tr>
            <tr v-for="(req, id) in Journal" :key="id" :value="id">
              <td>{{ id + 1 }}</td>
              <td>{{ req.fio }}</td>

              <td
                v-for="(val, id) in req.dt.slice(this.gran1, this.gran2)"
                :key="id"
                :value="id"
              >
                <div class="otmetki">
                  <div class="ych">{{ val.pos }}</div>

                  <div class="ych">{{ val.usp }}</div>

                  <div class="ych">{{ val.task }}</div>
                </div>
              </td>
                <td>{{ this.sumotm }}</td>
            </tr>
          </tbody>
        </table>
        <div class="page-wrapper">
          <div
            v-for="i in page"
            :key="i"
            :value="i"
            class="page"
            :class="{ 'current-page': pagenumber === i }"
          >
            <button type="button" class="btn" @click="CheckStr(i)">
              {{ i }}
            </button>
          </div>
        </div>
      </div>
    </transition>
    <div v-if="r === true && !gr | !discz | !student">
      <p style="color: red">Недостаточно данных, проверьте заполнение полей.</p>
    </div>
    <div
      v-if="
        r == true &&
        journalGroup.length == 0 &&
        gr &&
        discz &&
        student &&
        loading == false
      "
    >
      <h2 style="color: red">
        Журнал ещё не был заполнен.
      </h2>
    </div>
  </div>
  
  <div v-if="!user && error">
    <p style="color: red">Вы не вошли в аккаунт. Пожалуйста, авторизуйтесь.</p>
  </div>
  <div v-if="user && user.role != 'student'">
    <p style="color: red">У вас нет прав доступа к этой странице.</p>
  </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";
import { mapGetters } from "vuex";

export default {
  name: "JournalStudent",
  setup() {
    const show = ref(false);
    return { show };
  },
  data() {
    return {
      gr: "",
      discz: "",
      discs: [],
      student: "",
      backGroup: [],
      journalGroup: [],
      workload3: [],
      DateType: [],
      Journal: [],
      unique: [],
      backStud: [],
      r: false,
      sumotm: 0,
      gran1: 0,
      gran2: 5,
      page: 1,
      pagenumber: 0,
      date1: "",
      date2: "",
      loading: false,
    };
  },
  computed: {
    ...mapGetters(["user"]),
  },
  created() {
    setTimeout(() => {
      if (!this.user) {
        this.error = true;
        setTimeout(() => {
          this.$router.push("/");
          this.$store.dispatch("user", null);
        }, 100);
      }
      if (this.user && this.user.role != "student") {
        setTimeout(() => {
          this.$router.push("/profile");
        }, 100);
      }
    }, 1000);
  },
  methods: {
    CheckStr(i) {
      if (i == 1) {
        this.gran1 = 0;
        this.gran2 = 5;
        this.pagenumber = i;
      }
      if (i > 1) {
        this.gran1 = 0;
        this.gran2 = 5;
        this.gran1 = this.gran1 + 5 * (i - 1);
        this.gran2 = this.gran2 + 5 * (i - 1);
        this.pagenumber = i;
      }
    },
    button() {
      if ((!this.student & !this.gr & !this.discz) | (this.r == true)) {
        return true;
      }
      return false;
    },
    selectedStud() {
      this.CheckSTudGroup();
    },
    async CheckSTudGroup() {
      try {
        const response = await axios.get(`http://localhost:5000/perecgroup`, {
          params: {
            id: this.user.id,
          },
        });
        this.gr = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    selectedDisc() {
      this.Journal.push({
        id: this.user.id,
        fio: this.user.username,
        dt: [],
      });
      if (this.workload3.lenght != 0) {
        this.workload3 = [];
      }
      this.getInfoAboutWorkload3(this.group, this.discz);
    },
    selectedGroup() {
      this.fetchDiscs(this.gr);
    },
    async getInfoAboutWorkload3() {
      try {
        const response = await axios.get(`http://localhost:5000/workload3`, {
          params: {
            gr: this.gr,
            discz: this.discz,
          },
        });
        await response.data.forEach((val) => {
          this.workload3.push([val.id]);
        });
      } catch (err) {
        console.log(err);
      }
    },
    getALLJournal() {
      this.journalGroup.forEach((vl) => {
        const d1 = vl.date;
        const t1 = vl.type;
        const p1 = vl.otm_posh;
        const u1 = vl.otm_usp;
        const tsk1 = vl.task;
        this.Journal[0].dt.push({
          date: d1,
          type: t1,
          pos: p1,
          usp: u1,
          task: tsk1,
        });
        this.sumotm = this.sumotm + vl.otm_usp;
      });
      this.loading = true;
      setTimeout(() => {
          this.r = true;
          this.loading = false;
        }, 1000);
    },
    async getJournalST() {
      try {
        const response = await axios.get(
          `http://localhost:5000/journalgroupstudent`,
          {
            params: {
              workload: this.workload3,
              student: this.student,
              date1: this.date1,
              date2: this.date2
            },
          }
        );
        
        this.journalGroup = response.data;
        const dlina = this.journalGroup.length;
        this.page = Math.ceil(dlina / 5);
        this.getALLJournal();
        
      } catch (err) {
        console.log(err);
      }
    },
    async fetchDiscs() {
      try {
        const response = await axios.get(`http://localhost:5000/studentfisc`, {
          params: { gr: this.gr },
        });
        this.discs = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    Abnull() {
      (this.discz = ""),
        (this.r = false),
        (this.journalGroup = []),
        (this.workload3 = []),
        (this.dt = []),
        (this.Journal = []),
        (this.sumotm = 0),
        (this.date1 = ""),
        (this.date2 = ""),
        (this.loading = false);
    },
  },
};
</script>

<style>
div {
  text-align: center;
  font-size: medium;
}

b {
  color: #347c99;
}
table.iksweb {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  height: auto;
}
table.iksweb,
table.iksweb td,
table.iksweb th {
  border: 1px solid #595959;
}
table.iksweb td,
table.iksweb th {
  padding: 3px;
  width: 30px;
  height: 35px;
}
table.iksweb th {
  background: #347c99;
  color: #fff;
  font-weight: normal;
}
.page-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.page {
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px;
  margin-right: 5px;
}
.current-page {
  border: 3px solid #347c99;
  border-radius: 10px;
}
.otmetki {
  display: flex;
  justify-content: space-evenly;
}
.ych {
  border: 1px solid #595959;
  border-radius: 10px;
  padding: 7px;
}
</style>
