<template>
  <Nav />
  <main class="form-signin w-100 m-auto">
    <router-view />
  </main>
</template>

<script>
import Nav from "@/components/Nav.vue";
import axios from "axios";

export default {
  name: "App",
  components: {
    Nav,
  },
  async created() {
    try {
      const response = await axios.get("/profile");
      this.$store.dispatch("user", response.data);
    } catch (err) {
      console.error("Ошибка нахождения профиля пользователя", err);
      this.error =
        "Ошибка нахождения профиля пользователя. Пожалуйста, попробуйте снова." +
        err;
    }
  },
};
</script>
