import { createRouter, createWebHistory } from "vue-router";
import JournalWriteTeacher from "@/views/JournalWriteTeacher.vue";
import Profile from "@/views/Profile.vue";
import Login from "@/views/Login.vue";
import JournalStudent from "@/views/JournalStudent.vue";
import CheckTeacher from "@/views/CheckTeacher.vue";
import AdminPage from "@/views/AdminPage.vue";


const routes = [
  { path: "/profile", component: Profile },
  { path: "/journalwriteteacher", component: JournalWriteTeacher },
  { path: "/", component: Login },
  { path: "/checkstudent", component: JournalStudent },
  { path: "/checkteacher", component: CheckTeacher },
  { path: "/admin", component: AdminPage },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
