<template>
  <div v-if="user && !error">
    <h3 style="color: #347c99d6">
      <b>Страница профиля пользователя</b>
    </h3>
    <h3>
      Здравствуйте, <b> {{ user.username }}</b>
    </h3>
    <h3>
      Ваша роль: <b> {{ role }} </b>
    </h3>
    <h3 v-if="this.gr">
      Ваша группа: <b> {{ gr }} </b>
    </h3>
  </div>
  <h3 style="color: red" v-if="!user && error">Вы не вошли в систему!</h3>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "UProfile",
  data() {
    return {
      role: "",
      error: false,
      gr: "",
    };
  },
  computed: {
    ...mapGetters(["user"]),
  },
  methods: {
    rolecheck() {
      if (this.user.role == "teacher") {
        this.role = "ПРЕПОДАВАТЕЛЬ";
      }
      if (this.user.role == "student") {
        this.role = "СТУДЕНТ";
        this.gr = this.user.group;
      }
      if (this.user.role == "admin") {
        this.role = "АДМИНИСТРАТОР";
      }
    },
  },
  created() {
    setTimeout(() => {
      if (!this.user) {
        this.error = true;
        setTimeout(() => {
          this.$router.push("/");
          this.$store.dispatch("user", null);
        }, 10);
      }
      if (this.user) {
        setTimeout(() => {
          this.rolecheck();
        }, 5);
      }
    }, 100);
  },
  updated() {
    setTimeout(() => {
      if (!this.user) {
        this.error = true;
        setTimeout(() => {
          this.$router.push("/");
          this.$store.dispatch("user", null);
        }, 50);
      }
      if (this.user) {
        setTimeout(() => {
          this.rolecheck();
        }, 5);
      }
    }, 100);
  },
};
</script>
