<template>
  <div v-if="user && user.role == 'teacher'">
    <h2 style="color:#347c99d6">
      Страница просмотра журнала учёта посещаемости и успеваемости для
      преподавателя
    </h2>
    <form class="info-for-completion">
      <h2>Преподаватель:</h2>
      <select @change="selectedGroup()" v-model="teacher">
        <option disabled value="">Преподаватель</option>
        <option :value="user.id" required>{{ user.username }}</option>
      </select>
      <h2>Группа:</h2>
      <select @change="selectedDisc()" v-model="gr">
        <option disabled value="">Выберите группу</option>
        <option v-for="req in groups" :key="req.id">
          {{ req.group }}
        </option>
      </select>
      <h2>Выберите период, за который хотите получить данные:</h2>
      <div class="dateformat">
        <h2 class="dateitem">C:</h2>
        <input
          type="date"
          v-model="date1"
          required
          pattern="\d{4}-\d{2}-\d{2}"
          class="dateitem"
        />
        <h2 class="dateitem">По:</h2>
        <input
          type="date"
          v-model="date2"
          required
          pattern="\d{4}-\d{2}-\d{2}"
          class="dateitem"
        />
      </div>
      <h2>Дисциплина:</h2>
      <select @change="selectedWorkload()" v-model="discz">
        <option disabled value="">Выберите дисциплину</option>
        <option
          v-for="req in discs"
          :key="req.discipline"
          :value="req.discipline"
        >
          {{ req.name }}
        </option>
      </select>
    </form>
    <br/>
    <button
      :disabled="button()"
      type="button"
      class="btn"
      @click="getJournal()"
    >
      Найти журнал
    </button>
    <button
      type="button"
      @click="Abnull()"
      class="btn"
      style="margin-left: 10px"
    >
      Сбросить данные
    </button>
    <br />
    <div class="otmetkiupd" v-if="this.r == true && this.updateokno == true">
      <div class="conteiner">
        <p>Студент</p>
        <p>Дата</p>
        <p>Вид занятия</p>
        <p>Номер занятия</p>
      </div>
      <div class="conteiner">
        <p>{{ this.idstudent }}</p>
        <p>{{ this.datajourn }}</p>
        <p>{{ this.typechoose }}</p>
        <p>{{ this.nc + 1 }}</p>
      </div>
      <div class="conteiner">
        <p>Посещаемость</p>
        <p>Успеваемость</p>
        <p>Задание/Тема</p>
        <p></p>
      </div>
      <div class="conteiner">
        <input v-model="poseh" class="ych" ref="typeBox" id="typeBox" />
        <input v-model="usev" class="ych" />
        <input v-model="tasks" class="ych" />
        <button class="ych, btn" @click="Update()">Сохранить изменения</button>
      </div>
    </div>
    <div v-if="this.loading == true && this.r == false">
      <h1 style="color: #347c99">Загрузка ...</h1>
    </div>
    <div v-if="this.r === true && journalGroup.length != 0">
      <transition>
        <div>
          <p>Обозначения сокращений:</p>
          <p>
            Отметка посещаемости - "П", балл успеваемости - "Б", задание или
            тема занятия (не более 100 знаков) - "З/Т", изменение данных ячейки -
            "И"
          </p>
          <p>
            Отметки посещаемости: присутствие - "П", отсутствие - "О",
            отсутствие по уважительной причине - "ОПУ", отсутствие по болезни -
            "ОПБ"
          </p>
          <table class="iksweb">
            <tbody>
              <tr>
                <th rowspan="4">№</th>
                <th>Дата</th>
                <th
                  v-for="(val, id) in DateType.slice(this.gran1, this.gran2)"
                  :key="id"
                  :value="id"
                >
                  {{ val.dateform }}
                </th>
                <th rowspan="4">
                  Итоговый балл за период 
                <br>
                с 
                <br>
                {{ this.date1 }} 
                <br>
                по 
                <br>
                {{ this.date2 }}
                </th>
              </tr>
              <tr>
                <th>Вид занятия</th>
                <th
                  v-for="(val, id) in DateType.slice(this.gran1, this.gran2)"
                  :key="id"
                  :value="id"
                >
                  {{ val.type }}
                </th>
              </tr>

              <tr>
                <th>Номер занятия</th>
                <th
                  v-for="(val, id) in DateType.slice(this.gran1, this.gran2)"
                  :key="id"
                  :value="id"
                >
                  {{ val.number_classes + 1 }}
                </th>
              </tr>
              <tr>
                <th>Список студентов</th>
                <th
                  v-for="(val, id) in DateType.slice(this.gran1, this.gran2)"
                  :key="id"
                  :value="id"
                >
                  <div class="otmetki">
                    <div class="ych">П</div>
                    <div class="ych">Б</div>
                    <div class="ych">З/Т</div>
                    <div class="ych">И</div>
                  </div>
                </th>
              </tr>
              <tr v-for="(req, id) in Journal" :key="id" :value="id">
                <td>{{ id + 1 }}</td>
                <td>{{ req.name }} {{ req.patronymic }} {{ req.lastname }}</td>
                <td
                  v-for="(val, id) in req.dt.slice(this.gran1, this.gran2)"
                  :key="id"
                  :value="id"
                >
                  <div class="otmetki">
                    <div class="ych">{{ val.pos }}</div>

                    <div class="ych">{{ val.usp }}</div>

                    <div class="ych">{{ val.task }}</div>
                    <div class="ych">
                      <button
                        type="button"
                        @click="InfoForUpdate(req.id, val.num - 1)"
                        class="btn"
                      >
                        {{ val.num }}
                      </button>
                    </div>
                  </div>
                </td>
                <td>{{ req.sumbal }}</td>
              </tr>
            </tbody>
          </table>
          <div class="page-wrapper">
            <div
              v-for="i in page"
              :key="i"
              :value="i"
              class="page"
              :class="{ 'current-page': pagenumber === i }"
            >
              <button type="button" class="btn" @click="CheckStr(i)">
                {{ i }}
              </button>
            </div>
          </div>
        </div>
      </transition>
    </div>

    <div v-if="r === true && !gr | !discz | !teacher && loading == false">
      <h2 style="color: red">
        Недостаточно данных, проверьте заполнение полей.
      </h2>
    </div>
    <div
      v-if="
        r === true &&
        journalGroup.length == 0 &&
        gr &&
        discz &&
        discz &&
        teacher &&
        loading == false
      "
    >
      <h1 style="color: red">Журнал ещё не был заполнен, перейдите на страницу заполнения, чтобы добавить страницу журнала.</h1>
    </div>
  </div>
  <div v-if="!user && error">
    <p style="color: red">
      Вы не вошли в аккаунт. Пожалуйста, авторизуйтесь.
    </p>
  </div>
  <div v-if="user && user.role != 'teacher'">
    <p style="color: red">У вас нет прав доступа к этой странице.</p>
  </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";
import { mapGetters } from "vuex";

export default {
  name: "CheckTeacher",
  setup() {
    const show = ref(false);
    return { show };
  },
  data() {
    return {
      teacher: "",
      gr: "",
      discz: "",
      groups: [],
      discs: [],
      backGroup: [],
      journalGroup: [],
      workload2: [],
      DateType: [],
      Journal: [],
      unique: [],
      r: false,
      sumotm: 0,
      otm: [],
      gran1: 0,
      gran2: 5,
      page: 1,
      pagenumber: 0,
      poseh: "",
      usev: "",
      tasks: "",
      idstudent: "",
      idzan: "",
      idwork: "",
      datajourn: "",
      idjr: "",
      nc: "",
      nums: 0,
      updateokno: false,
      typechoose: "",
      loading: false,
      date1: "",
      date2: "",
    };
  },
  computed: {
    ...mapGetters(["user"]),
  },
  created() {
    setTimeout(() => {
      if (!this.user) {
        this.error = true;
        setTimeout(() => {
          this.$router.push("/");
          this.$store.dispatch("user", null);
        }, 100);
      }
      if (this.user && this.user.role != "teacher") {
        setTimeout(() => {
          this.$router.push("/profile");
        }, 100);
      }
    }, 1000);
  },
  methods: {
    async Update() {
      try {
        const response = await axios.get(`http://localhost:5000/update`, {
          params: {
            id: this.idjr,
            otm_posh: this.poseh,
            otm_usp: this.usev,
            task: this.tasks,
          },
        });
        this.Journal = [];
        this.journalGroup = [];
        this.getJournal();
        this.updateokno = false;
      } catch (err) {
        console.log(err);
      }
    },
    InfoForUpdate(id1, id2) {
      window.requestAnimationFrame(() => this.$refs.typeBox.focus());
      this.loading = true;
      this.Journal.forEach((val) => {
        if (val.id == id1) {
          this.usev = val.dt[id2].usp;
          this.poseh = val.dt[id2].pos;
          this.tasks = val.dt[id2].task;
          this.idwork = val.dt[id2].idworkload;
          this.idjr = val.dt[id2].idjournal;
          this.idstudent = val.name + " " + val.patronymic + " " + val.lastname;
          this.idzan = id2;
        }
        this.journalGroup.forEach((vl) => {
          if (vl.id == this.idjr) {
            this.datajourn = vl.dateform;
            this.typechoose = vl.type;
            this.nc = vl.number_classes;
          }
        });
      });
      this.updateokno = true;
    },
    CheckStr(i) {
      if (i == 1) {
        this.gran1 = 0;
        this.gran2 = 5;
        this.pagenumber = i;
      }
      if (i > 1) {
        this.gran1 = 0;
        this.gran2 = 5;
        this.gran1 = this.gran1 + 5 * (i - 1);
        this.gran2 = this.gran2 + 5 * (i - 1);
        this.pagenumber = i;
      }
    },
    funct() {
      this.Journal.forEach((val) => {
        val.dt.forEach((v) => {
          this.sumotm = this.sumotm + v.usp;
        });
        val.sumbal = this.sumotm;
        this.sumotm = 0;
      });
    },
    button() {
      if ((!this.teacher & !this.gr & !this.discz) | (this.r == true)) {
        return true;
      }
      return false;
    },
    Abnull() {
      (this.gr = ""),
        (this.discz = ""),
        (this.groups = []),
        (this.discs = []),
        (this.backGroup = []),
        (this.journalGroup = []),
        (this.workload2 = []),
        (this.DateType = []),
        (this.Journal = []),
        (this.unique = []),
        (this.teacher = ""),
        (this.r = false),
        (this.date1 = ""),
        (this.date2 = "");
    },
    selectedGroup() {
      this.getInfoByTeacherGroup(this.teacher);
    },
    selectedDisc() {
      this.getInfoByTeacherDisc(this.teacher, this.gr);
    },
    selectedWorkload() {
      this.getInfoAboutWorkload2(this.teacher, this.gr, this.discz);
      this.fetchGroup(this.gr);
    },
    async getInfoByTeacherGroup(teacher) {
      try {
        const response = await axios.get(
          `http://localhost:5000/teachers/${teacher}`
        );
        this.groups = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getInfoByTeacherDisc() {
      try {
        const response = await axios.get(`http://localhost:5000/discipline`, {
          params: { teacher: this.teacher, gr: this.gr },
        });
        this.discs = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getInfoByTeacherType() {
      try {
        const response = await axios.get(`http://localhost:5000/type`, {
          params: { teacher: this.teacher, gr: this.gr, discz: this.discz },
        });
        this.types = response.data;
      } catch (err) {
        console.log(err);
      }
    },

    async fetchGroup() {
      try {
        const response = await axios.get(`http://localhost:5000/group`, {
          params: { gr: this.gr },
        });
        this.backGroup = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getInfoAboutWorkload2() {
      try {
        const response = await axios.get(`http://localhost:5000/workload2`, {
          params: { teacher: this.teacher, gr: this.gr, discz: this.discz },
        });
        await response.data.forEach((val) => {
          this.workload2.push([val.id]);
        });

        const resp = await axios.get(`http://localhost:5000/datetype`, {
          params: {
            workload: this.workload2,
            date1: this.date1,
            date2: this.date2,
          },
        });
        this.DateType = resp.data;
        const dlina = this.DateType.length;
        this.page = Math.ceil(dlina / 5);
      } catch (err) {
        console.log(err);
      }
    },
    async getJournal() {
      try {
        const response = await axios.get(`http://localhost:5000/journalgroup`, {
          params: {
            workload: this.workload2,
            date1: this.date1,
            date2: this.date2,
          },
        });
        this.journalGroup = response.data;
        this.getALLJournal();
      } catch (err) {
        console.log(err);
      }
    },
    async getALLJournal() {
      try {
        const response = await axios.get(`http://localhost:5000/journal`, {
          params: { workload: this.workload2 },
        });
        this.backGroup.forEach((val) => {
          this.Journal.push({
            id: val.id,
            name: val.name,
            patronymic: val.patronymic,
            lastname: val.lastname,
            sumbal: 0,
            dt: [],
          });
        });

        this.Journal.forEach((val) => {
          this.journalGroup.forEach((vl) => {
            const d1 = vl.dateform;
            const t1 = vl.type;
            const p1 = vl.otm_posh;
            const u1 = vl.otm_usp;
            const tsk1 = vl.task;
            const q = vl.id;
            const w = vl.workload;
            const e = vl.date;

            if (val.id === vl.student) {
              this.nums = this.nums + 1;
              val.dt.push({
                num: this.nums,
                date: d1,
                type: t1,
                pos: p1,
                usp: u1,
                task: tsk1,
                idjournal: q,
                idworkload: w,
                datejournal: e,
              });
            }
          });
          this.nums = 0;
        });
        this.funct();
        this.loading = true;
        setTimeout(() => {
          this.r = true;
          this.loading = false;
        }, 3000);
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style>
table.iksweb {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  height: auto;
}
table.iksweb,
table.iksweb td,
table.iksweb th {
  border: 1px solid #595959;
}
table.iksweb td,
table.iksweb th {
  padding: 3px;
  width: 30px;
  height: 35px;
}
table.iksweb th {
  background: #347c99;
  color: #fff;
  font-weight: normal;
}
.page-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.page {
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px;
  margin-right: 5px;
}
.current-page {
  border: 3px solid #347c99;
  border-radius: 10px;
}
.otmetki {
  display: flex;
  justify-content: space-evenly;
}
.ych {
  border: 1px solid #595959;
  border-radius: 10px;
  padding: 7px;
}
.otmetkiupd {
  display: flex;
  justify-content: center;
  padding: 7px;
  margin-top: 15px;
}
.conteiner {
  display: grid;
  row-gap: 1em;
  margin-left: 15px;
}
.dateformat {
  display: flex;
  justify-content: center;
  margin: 15px;
}
.dateitem {
  padding: 7px;
}
</style>
