<template>
  <div class="form-signin">
    <form @submit.prevent="login">
      <h1 class="h3 mb-3 fw-normal">Вход в журнал</h1>
      <p v-if="error" class="error-message">{{ error }}</p>
      <div class="form-floating">
        <input
          v-model="username"
          class="form-control"
          placeholder="Фамилия Имя Отчество"
        />
        <label>Логин</label>
      </div>
      <div class="form-floating">
        <input
          :type="showPassword ? 'text' : 'password'"
          v-model="password"
          class="form-control"
          placeholder="Введите пароль"
        />
        <label>Пароль</label>
        <i
          class="icon"
          :class="showPassword ? 'fa-solid fa-eye-slash' : 'fa-solid fa-eye'"
          @click="showPassword = !showPassword"
        ></i>
      </div>
      <div class="form-floating">
        <select class="form-control" v-model="role">
          <option disabled value="">Роль</option>
          <option value="teacher">Преподаватель</option>
          <option value="student">Студент</option>
          <option value="admin">Администратор</option>
        </select>
        <label>Роль</label>
      </div>
      <button class="btn btn-primary w-100 py-2" type="submit">
        Войти в аккаунт
      </button>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ULogin",
  data() {
    return {
      username: "",
      password: "",
      role: "",
      error: "",
      showPassword: false,
    };
  },
  methods: {
    async login() {
      try {
        const response = await axios.post("/login", {
          username: this.username,
          password: this.password,
          role: this.role,
        });
        localStorage.setItem("token", response.data.token);
        this.$store.dispatch("user", response.data.user[0]); 
        this.$router.push("/profile");
      } catch (error) {
        console.error(
          "Login failed",
          error.response?.data.message || "Произошла непредвиденная ошибка"
        );
        this.showError(
          error.response?.data.message || "Произошла непредвиденная ошибка"
        );
      }
    },
    showError(errorMessage) {
      this.error = errorMessage;
      setTimeout(() => {
        this.error = "";
        this.password = "";
        this.username = "";
        this.password = "";
        this.showPassword = false;
      }, 3000);
    },
  },
};
</script>

<style scoped>
.form-floating {
  margin-bottom: 10px;
}

form {
  max-width: 330px;
  padding: 1rem;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

button {
  background-color: #347c99;
}

button:hover {
  background-color: #347c99d6;
  transition: 0.7s;
}
.icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #666;
  transition: color 0.3s ease;
  margin-right: 10px;
}
</style>
