<template>
  <div v-if="user && user.role == 'admin'">
    <h2 style="color: #347c99d6">
      Страница администратора для регистрирования учётных записей
    </h2>
    <h2>Выберите роль регистрируемого:</h2>
    <select @change="selectedRole()" v-model="rolereq">
      <option disabled value="">Роль</option>
      <option value="teacher">Преподаватель</option>
      <option value="student">Студент</option>
    </select>
    <div v-if="rolereq == 'student'">
      <h2>Выберите группу студента:</h2>
      <select @change="selectedINFO()" v-model="group">
        <option v-for="req in groups" :key="req.group" :value="req.group">
          {{ req.group }}
        </option>
      </select>
    </div>
    <div v-if="rolereq == 'teacher'">
      <h2>Выберите факультет:</h2>
      <select @change="selectedINFO()" v-model="faculty">
        <option
          v-for="req in facultys"
          :key="req.division_curriculum"
          :value="req.division_curriculum"
        >
          {{ req.division_curriculum }}
        </option>
      </select>
    </div>
    <h2>Выберите ФИО регистрируемого:</h2>
    <select @change="selectedFIO()" v-model="login">
      <option disabled value="">ФИО</option>
      <option v-for="req in spisok" :key="req.id" :value="req.id">
        {{ req.username }}
      </option>
    </select>
    <br />
    <h3 style="color: red" v-if="login && this.checkpassword != null">
      Пользователю уже задан пароль
    </h3>
    <div v-if="login && this.checkpassword == null">
      <h2>Задайте пароль:</h2>
      <div class="input-group">
        <input
          :type="showPassword ? 'text' : 'password'"
          v-model="password"
          placeholder="Введите пароль"
        />
        <i
          class="icon"
          :class="showPassword ? 'fa-solid fa-eye-slash' : 'fa-solid fa-eye'"
          @click="showPassword = !showPassword"
        ></i>
      </div>
      <h3 style="color: #347c99d6" v-if="this.save == true">
        Успешно сохранено
      </h3>
    </div>
    <br />
    <button
      :disabled="button()"
      type="button"
      @click="sendDataAdmin()"
      class="btn"
    >
      Сохранить данные
    </button>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";

export default {
  name: "AdminPage",
  data() {
    return {
      rolereq: "",
      login: "",
      group: "",
      spisok: [],
      groups: [],
      faculty: "",
      facultys: [],
      password: "",
      save: "",
      checkpassword: "",
      showPassword: false,
    };
  },
  computed: {
    ...mapGetters(["user"]),
  },
  created() {
    setTimeout(() => {
      if (!this.user) {
        this.error = true;
        setTimeout(() => {
          this.$router.push("/");
          this.$store.dispatch("user", null);
        }, 100);
      }
      if (this.user && this.user.role != "admin") {
        setTimeout(() => {
          this.$router.push("/profile");
        }, 100);
      }
    }, 1000);
  },
  methods: {
    selectedFIO() {
      this.CheckUser();
    },
    async CheckUser() {
      try {
        const response = await axios.get(`http://localhost:5000/checkaccaunt`, {
          params: { role: this.rolereq, id: this.login },
        });
        this.checkpassword = response.data[0].password;
      } catch (err) {
        console.log(err);
      }
    },
    button() {
      if (!this.rolereq && !this.login && !this.group) {
        return true;
      }
      return false;
    },
    selectedRole() {
      if (this.rolereq == "teacher") {
        this.getListUserFaculty();
      }
      if (this.rolereq == "student") {
        this.getListUserGroup();
      }
    },
    selectedINFO() {
      this.getListUser();
    },
    async getListUser() {
      try {
        const response = await axios.get(`http://localhost:5000/listuser`, {
          params: {
            role: this.rolereq,
            group: this.group,
            division_curriculum: this.faculty,
          },
        });
        this.spisok = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getListUserGroup() {
      try {
        const response = await axios.get(
          `http://localhost:5000/listuserstudent`
        );
        this.groups = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getListUserFaculty() {
      try {
        const response = await axios.get(
          `http://localhost:5000/listuserfaculty`
        );
        this.facultys = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async sendDataAdmin() {
      try {
        const response = await axios.get(
          `http://localhost:5000/saveinfoaccount`,
          {
            params: {
              role: this.rolereq,
              id: this.login,
              password: this.password,
            },
          }
        );
        if (response) {
          setTimeout(() => {
            this.save = true;
            setTimeout(() => {
              (this.rolereq = ""),
                (this.login = ""),
                (this.group = ""),
                (this.spisok = []),
                (this.groups = []),
                (this.faculty = ""),
                (this.facultys = []),
                (this.password = ""),
                (this.checkpassword = ""),
                (this.showPassword = false),
                (this.save = false);
            }, 1000);
          }, 100);
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style>
.input-group {
  position: relative;
  width: 300px;
  margin: 20px auto;
}

.icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #666;
  transition: color 0.3s ease;
  margin-right: 10px;
}

.icon:hover {
  color: #333;
}
</style>
