<template>
  <div v-if="user && user.role == 'teacher'">
    <h2 style="color: #347c99d6">
      Страница заполнения журнала учёта успеваемости и посещаемости обучающихся
    </h2>
    <form class="info-for-completion">
      <h2>Преподаватель:</h2>
      <select @change="selectedGroup()" v-model="teacher">
        <option value="" disabled selected>Преподаватель</option>
        <option :value="user.id">{{ user.username }}</option>
      </select>
      <h2>Группа:</h2>
      <select @change="selectedDisc()" v-model="gr">
        <option value="" disabled>Выберите группу</option>
        <option v-for="req in groups" :key="req.id">
          {{ req.group }}
        </option>
      </select>
      <h2>Дисциплина:</h2>
      <select @change="selectedType()" v-model="discz">
        <option value="" disabled>Выберите дисциплину</option>
        <option
          v-for="req in discs"
          :key="req.discipline"
          :value="req.discipline"
        >
          {{ req.name }}
        </option>
      </select>
      <h2>Выберите дату:</h2>
      <input
        type="date"
        v-model="date"
        :max="maxDate"
        required
        pattern="\d{4}-\d{2}-\d{2}"
      />
      <h2>Тип занятия:</h2>
      <select @change="selectedWorkload()" v-model="type">
        <option value="" disabled>Выберите тип занятия</option>
        <option v-for="req in types" :key="req.load" :value="req.load">
          {{ req.type }}
        </option>
      </select>
      <h2>Введите номер занятия:</h2>
      <h4>
        (Если за день было несколько повторяющихся пар с одинаковым видом
        работы, то выберите номер заполняемого занятия, если пара была одна или
        вид работ разный, то оставьте 1)
      </h4>
      <select v-model="kolvo">
        <option value="0" selected>1</option>
        <option value="1">2</option>
        <option value="2">3</option>
        <option value="3">4</option>
        <option value="4">5</option>
        <option value="5">6</option>
      </select>
    </form>
    <br />
    <button
      :disabled="button()"
      type="button"
      @click="
        fetchGroup(this.gr);
        sendData();
      "
      class="btn"
    >
      Создать страницу журнала
    </button>
    <button
      type="button"
      @click="Abnull()"
      class="btn"
      style="margin-left: 10px"
    >
      Сбросить данные
    </button>
    <div v-if="this.signal == true">
      <h1 style="color: red">Балл не соответствует виду занятия</h1>
    </div>
    <transition>
      <div class="table">
        <div v-if="r == true">
          <h1>Страница журнала группы {{ gr }}</h1>
          <table class="iksweb">
            <tbody>
              <tr>
                <th rowspan="4">Список студентов</th>
              </tr>
              <tr>
                <th colspan="3">Дата</th>
              </tr>
              <tr>
                <td colspan="3">{{ date }}</td>
              </tr>
              <tr>
                <th>Посещаемость</th>
                <th>Успеваемость</th>
                <th>Задание/Тема</th>
              </tr>
            </tbody>
            <tbody>
              <tr
                v-for="(req, i) in backGroup"
                :key="req.id"
                :value="req.id"
                ref="tr"
              >
                <td>{{ req.name }} {{ req.patronymic }} {{ req.lastname }}</td>

                <td>
                  <select
                    @change="selectedPosh($event, i, req.id)"
                    v-model="backGroup[i].otm_posh"
                  >
                    <option value="" disabled selected>
                      Отметка присутствия
                    </option>
                    <option value="П">Присутствие</option>
                    <option value="О">Отсутствие</option>
                    <option value="ОПУ">
                      Отсутствие по уважительной причине
                    </option>
                    <option value="ОПБ">Отсутствие по болезни</option>
                  </select>
                </td>
                <td>
                  <input
                    type="number"
                    @change="selectedOtm($event, i + 1, req.id)"
                    v-model="backGroup[i].otm_usp"
                  />
                </td>
                <td>
                  <input
                    type="text"
                    @change="selectedTask($event, i, req.id)"
                    v-model="backGroup[i].task"
                  />
                </td>
              </tr>
            </tbody>
          </table>
          <button class="btn btn-primary w-100 py-2" @click="Save()">
            Сохранить
          </button>
        </div>
      </div>
    </transition>

    <div v-if="save == true"><h3>Успешно сохранено!</h3></div>
    <div v-if="errorsave2 == true">
      <h3 style="color: red">
        Не удалось сохранить данные, попробуйте ещё раз!
      </h3>
    </div>
    <div v-if="errorsave == true">
      <h3 style="color: red">
        Данные по текущему занятию уже существуют, пожалуйста, проверьте
        корректность введённых данных.
      </h3>
    </div>
    <div v-if="!user && error">
      <p style="color: red">
        Вы не вошли в аккаунт. Пожалуйста, авторизуйтесь.
      </p>
    </div>
    <div v-if="user && user.role != 'teacher'">
      <p style="color: red">У вас нет прав доступа к этой странице</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";

export default {
  name: "JournalWriteTeacher",
  data() {
    return {
      teacher: "",
      gr: "",
      discz: "",
      date: "",
      type: "",
      zadanie: "",
      backTeacher: [],
      groups: [],
      discs: [],
      types: [],
      backGroup: [],
      workload: [],
      resGroup: [],
      backJournal: [],
      worklodid: "",
      show: false,
      save: false,
      kolv: [],
      r: false,
      error: false,
      kolvo: "0",
      errorsave: false,
      errorsave2: false,
      signal: false,
      errorscheck: []
    };
  },
  computed: {
    ...mapGetters(["user"]),
    maxDate() {
      const datenow = new Date().toISOString().split("T")[0];
      return datenow;
    },
  },
  created() {
    setTimeout(() => {
      if (!this.user) {
        this.error = true;
        setTimeout(() => {
          this.$router.push("/");
          this.$store.dispatch("user", null);
        }, 100);
      }
      if (this.user && this.user.role != "teacher") {
        setTimeout(() => {
          this.$router.push("/profile");
        }, 100);
      }
    }, 1000);
  },
  methods: {
    Abnulltabl() {
      setTimeout(() => {
        this.save = false;
        (this.teacher = ""),
          (this.gr = ""),
          (this.discz = ""),
          (this.teacher = ""),
          (this.date = ""),
          (this.type = ""),
          (this.zadanie = "");
      }, 1000);
      (this.backJournal = []),
        (this.backGroup = []),
        (this.worklodid = ""),
        (this.groups = []),
        (this.discs = []),
        (this.workload = []),
        (this.resGroup = []),
        (this.types = []),
        (this.show = false),
        (this.r = false),
        (this.kolv = []),
        (this.errorsave = false),
        (this.errorsave2 = false);
    },
    Abnull() {
      (this.r = false), (this.save = false);
      (this.teacher = ""),
        (this.gr = ""),
        (this.discz = ""),
        (this.teacher = ""),
        (this.date = ""),
        (this.type = ""),
        (this.zadanie = ""),
        (this.types = []),
        (this.errorsave = false),
        (this.kolv = []),
        (this.backJournal = []),
        (this.backGroup = []),
        (this.worklodid = ""),
        (this.groups = []),
        (this.discs = []),
        (this.workload = []),
        (this.resGroup = []),
        (this.show = false),
        (this.errorsave = false),
        (this.errorsave2 = false),
        (this.errorscheck = []);
    },
    selectedGroup() {
      this.getInfoByTeacherGroup(this.teacher);
    },
    selectedDisc() {
      this.getInfoByTeacherDisc(this.teacher, this.gr);
    },
    selectedType() {
      this.getInfoByTeacherType(this.teacher, this.gr, this.discz);
    },
    selectedWorkload() {
      this.getInfoAboutWorkload(
        this.teacher,
        this.group,
        this.discz,
        this.type
      );
    },
    selectedOtm(event, i, val) {
      if (
        event.target.value < 71 &&
        this.type != 2 &&
        this.type != 3 &&
        this.type != 9 &&
        this.backGroup[i - 1].otm_posh == "П"
      ) {
        this.backGroup.forEach((el) => {
          if (el.id === val) {
            el.otm_usp = event.target.value;
          }
        });
        this.backJournal.forEach((el) => {
          if (el[1] === val) {
            el[3] = event.target.value;
          }
        });
      }
      if (
        event.target.value <= 30 &&
        (this.type == 2) | (this.type == 3) | (this.type == 9) &&
        this.backGroup[i - 1].otm_posh == "П"
      ) {
        this.backGroup.forEach((el) => {
          if (el.id === val) {
            el.otm_usp = event.target.value;
          }
        });
        this.backJournal.forEach((el) => {
          if (el[1] === val) {
            el[3] = event.target.value;
          }
        });
      }
      if (
        (event.target.value > 30 &&
          (this.type == "2") | (this.type == "3") | (this.type == "9")) |
        (event.target.value > 70 &&
          this.type != "2" &&
          this.type != "3" &&
          this.type != "9") |
        (this.backGroup[i - 1].otm_posh != "П")
      ) {
        this.backGroup[i - 1].otm_usp = "0";
        this.signal = true;
        setTimeout(() => {
          this.signal = false;
        }, 2000);
      }
    },
    selectedTask(event, i, val) {
      this.backGroup.forEach((el) => {
        if (el.id === val) {
          el.task = event.target.value;
        }
      });

      this.backJournal.forEach((el) => {
        if (el[1] === val) {
          el[5] = event.target.value;
        }
      });
    },
    selectedPosh(event, i, val) {
      this.backGroup.forEach((el) => {
        if (el.id === val) {
          el.otm_posh = event.target.value;
        }
      });
      this.backJournal.forEach((el) => {
        if (el[1] === val) {
          el[4] = event.target.value;
        }
      });
    },
    async sendData() {
      try {
        const response = await axios.get(`/howclasses`, {
          params: {
            date: this.date,
            workload: this.worklodid,
            number_classes: this.kolvo,
          },
        });

        this.kolv = response.data;
        const dlin = this.kolv.length;

        if (dlin > 0) {
          this.errorsave = true;
          this.r = false;
          setTimeout(() => {
            this.Abnull();
          }, 3000);
        }
        if (dlin == 0) {
          this.r = true;
          this.backJournal.forEach((el) => {
            el[6] = this.kolvo;
          });
        }
      } catch (err) {
        console.log(err);
      }
    },
    async SendData2() {
      try {
        const resp = await axios.get(`/saveJournal`, {
          params: { backGroup: this.backJournal },
        });
        this.errorscheck.push(resp);
      } catch (err) {
        console.log(err);
      }
    },
    async Save() {
      try {
        this.SendData2();
        if (this.errorscheck.length == 0){
        this.save = true;
        setTimeout(() => {
          this.Abnulltabl();
        }, 1000);}
      } catch (err) {
        console.log(err);
        this.errorsave2 = true;
        setTimeout(() => {
          this.Abnulltabl();
          this.errorsave2 = false;
        }, 1000);
      }
    },
    button() {
      if (
        (!this.teacher && !this.gr && !this.discz && !this.date && !this.type) |
        (this.r == true)
      ) {
        return true;
      }
      return false;
    },
    async fetchTeachers() {
      try {
        const response = await axios.get("/teachers");
        this.backTeacher = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getInfoByTeacherGroup(teacher) {
      try {
        const response = await axios.get(`/teachers/${teacher}`);
        this.groups = response.data;
      } catch (err) {
        console.log(err);
      }
    },

    async getInfoByTeacherDisc() {
      try {
        const response = await axios.get(`/discipline`, {
          params: { teacher: this.teacher, gr: this.gr },
        });
        this.discs = response.data;
      } catch (err) {
        console.log(err);
      }
    },

    async getInfoByTeacherType() {
      try {
        const response = await axios.get(`/type`, {
          params: { teacher: this.teacher, gr: this.gr, discz: this.discz },
        });
        this.types = response.data;
      } catch (err) {
        console.log(err);
      }
    },

    async getInfoAboutWorkload() {
      try {
        const response = await axios.get(`/workload`, {
          params: {
            teacher: this.teacher,
            gr: this.gr,
            discz: this.discz,
            type: this.type,
          },
        });
        this.workload = response.data;
        this.worklodid = response.data[0].id;
      } catch (err) {
        console.log(err);
      }
    },

    async fetchGroup() {
      try {
        const response = await axios.get(`/group`, {
          params: { gr: this.gr },
        });
        response.data.forEach((val) => {
          this.backGroup.push({
            workload: this.worklodid,
            name: val.name,
            patronymic: val.patronymic,
            lastname: val.lastname,
            id: val.id,
            date: this.date,
            otm_posh: "П",
            otm_usp: "0",
            task: "-",
          });
        });

        this.backGroup.forEach((val) => {
          this.backJournal.push([
            val.workload,
            val.id,
            val.date,
            "0",
            "П",
            "",
            0,
          ]);
        });
        response.data.forEach((val) => {
          this.resGroup.push(val.id);
        });
      } catch (err) {
        console.log(err);
      }
      this.show = true;
    },
  },
};
</script>

<style>
.btn {
  background-color: #347c99;
}

.btn {
  background-color: #347c99d6;
  transition: 0.7s;
}
div {
  text-align: center;
  font-size: medium;
}
table.iksweb {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  height: auto;
}
table.iksweb,
table.iksweb td,
table.iksweb th {
  border: 1px solid #595959;
}
table.iksweb td,
table.iksweb th {
  padding: 3px;
  width: 30px;
  height: 35px;
}
table.iksweb th {
  background: #347c99;
  color: #fff;
  font-weight: normal;
}
</style>
