<template>
  <header
    class="d-flex flex-wrap align-items-center justify-content-center justify-content-md-between py-3 mb-4 border-bottom"
    style="background-color: #347c99"
  >
    <a class="navbar-brand text-white"
      >Журнал учёта успеваемости и посещаемости обучающихся</a
    >
    <div class="col-md-3 text-end" v-if="!user">
      <button type="button" class="btn btn-outline-light me-2">
        Вход в аккаунт
      </button>
    </div>
    <div v-if="user">
      <ul
        class="nav col-12 col-lg-auto me-lg-auto mb-2 justify-content-center mb-md-0"
      >
        <li>
          <router-link to="/profile" class="nav-link px-2 text-white"
            >Профиль</router-link
          >
        </li>
        <li v-if="user.role == 'teacher'">
          <router-link
            to="/journalwriteteacher"
            class="nav-link px-2 text-white"
            >Заполнение журнала</router-link
          >
        </li>
        <li v-if="user.role == 'teacher'">
          <router-link to="/checkteacher" class="nav-link px-2 text-white"
            >Просмотр журнала</router-link
          >
        </li>
        <li v-if="user.role == 'student'">
          <router-link to="/checkstudent" class="nav-link px-2 text-white"
            >Просмотр журнала</router-link
          >
        </li>
        <li v-if="user.role == 'admin'">
          <router-link to="/admin" class="nav-link px-2 text-white"
            >Регистрация пользователей</router-link
          >
        </li>
        <li>
          <div class="col-md-3 text-end">
            <button
              type="button"
              class="btn btn-outline-light me-2"
              @click="LogOut"
            >
              Выход
            </button>
          </div>
        </li>
      </ul>
    </div>
  </header>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "UNav",
  methods: {
    LogOut() {
      localStorage.removeItem("token");
      this.$router.push("/");
      this.$store.dispatch("user", null);
    },
  },
  computed: {
    ...mapGetters(["user"]),
  },
};
</script>

<style scoped>
header {
  padding: 14px 16px;
}
</style>
